import React, {Component} from 'react';

class Home extends Component{
	render(){

        // Vars

		return(
			<React.Fragment>
				<section className="hero-wrap js-fullheight">
					<div className="overlay">
					</div>
					<div className="container">
						<div className="row no-gutters slider-text js-fullheight justify-content-center align-items-center">
							<div className="col-lg-8 col-md-6 d-flex align-items-center">
								<div className="text text-center home-center">
									<span className="subheading">Hey! I am</span>
									<h1>Francisco De Oliveira</h1>
									<h2>I'm a <span className="txt-rotate" data-period="2000" data-rotate='[ "n Informatic Engineer.", "Developer.", "Web Developer.", "Mobile Developer.", "Gamer." ]'></span></h2>
								</div>
							</div>
						</div>
					</div>
				</section>
			</React.Fragment>
		);
	}
}

export default Home;