import React, {Component} from 'react';
import './Contact.css';
import axios from 'axios';

class Contact extends Component{
    
    constructor(props){
        super(props)

        this.state = {
            name: '',
            email: '',
            subject: '',
            message: ''
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    }

    submitHandler = (e) => {
        e.preventDefault();
        document.getElementById("form-name").value = "";
        document.getElementById("form-email").value = "";
        document.getElementById("form-subject").value = "";
        document.getElementById("form-area").value = "";
        let button = document.getElementById("form-button");
        button.disabled = true;
        button.value = "Please wait...";
        axios.post('https://methodologic.com.ve/ncelizabeth/api/apiportfolio', this.state)
            .then(response => {
                alert("Tu mensaje ha sido enviado correctamente.");
                button.disabled = false;
                button.value = "Send Message";
            })
            .catch(error =>{
                alert("Error al envíar el mensaje, por favor intenta nuevamente.");
                button.disabled = false;
                button.value = "Send Message";
            })
    }

	render(){

        const { name, email, subject, message } = this.state

		return(
			<React.Fragment>
                <section className="ftco-section contact-section ftco-no-pb" id="contact-section">
                    <div className="container">
                        <div className="row justify-content-center mb-5 pb-3">
                            <div className="col-md-7 heading-section text-center">
                                <h1 className="big big-2">Contact</h1>
                                <h2 className="mb-4">Contact Me</h2>
                                <p>If you have any ideas to develop, do not hesitate to contact me.</p>
                            </div>
                        </div>

                        <div className="row no-gutters block-9">
                            <div className="col-md-6 order-md-last d-flex">
                                <form id="form-message" onSubmit={this.submitHandler} className="bg-light p-4 p-md-5 contact-form">
                                    <div className="form-group">
                                        <input id="form-name" name="name" value={name} onChange={this.changeHandler} type="text" className="form-control" placeholder="Your Name" required/>
                                    </div>
                                    <div className="form-group">
                                        <input id="form-email" name="email" value={email} onChange={this.changeHandler} type="email" className="form-control" placeholder="Your Email" required/>
                                    </div>
                                    <div className="form-group">
                                        <input id="form-subject" name="subject" value={subject} onChange={this.changeHandler} type="text" className="form-control" placeholder="Subject" required/>
                                    </div>
                                    <div className="form-group">
                                        <textarea id="form-area" name="message" value={message} onChange={this.changeHandler} cols="30" rows="7" className="form-control" placeholder="Message" required></textarea>
                                    </div>
                                    <div className="form-group">
                                        <input id="form-button" type="submit" value="Send Message" className="btn btn-primary py-3 px-5"/>
                                    </div>
                                </form>
                            </div>

                            <div className="col-md-6 d-flex">
                                <div className="img image"></div>
                            </div>
                        </div>
                    </div>
                </section>
			</React.Fragment>
		);
	}
}

export default Contact;