import React from 'react';
import ReactDOM from 'react-dom';
import Header from './components/1.header/Header';
import Home from './components/2.home/Home';
import About from './components/3.about/About';
import Resume from './components/4.resume/Resume';
import Services from './components/5.services/Services';
import Projects from './components/6.projects/Projects';
import Contact from './components/8.contact/Contact';
import Footer from './components/9.footer/Footer';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(
  <React.StrictMode>
    <Header />
    <Home />
    <About />
    <Resume />
    <Services />
    <Projects />
    <Contact />
    <Footer />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
